.image-container{width:17.5vw;height:17.5vw;position:relative}.image-container .tape{position:absolute;top:-15%;left:45%;width:10%;height:25%;background-color:#eee;opacity:.75}.image-container .image{width:100%;height:100%;background-color:#f5f5f5;justify-self:center;margin-bottom:50px;box-shadow:0 0 5px 0px rgba(0,0,0,.5);-webkit-transform-origin:center top;transform-origin:center top;display:flex;justify-content:center;align-items:center}.image-container .image>img{width:90%;height:90%;object-fit:cover}
.service-point{margin:0 20px;text-align:center}.service-point:hover .icon-container img:nth-child(1){opacity:0}.service-point:hover .icon-container img:nth-child(2){opacity:1}.service-point .icon-container{position:relative;height:95px;display:flex;justify-content:center}.service-point .icon-container img{position:absolute;max-height:75px;margin-bottom:20px;transition:.25s}.service-point .icon-container img:nth-child(2){opacity:0}.service-point h3{font-size:24px;margin-bottom:20px}.service-point p{font-size:16px}.service-point:first-child{margin-left:0}.service-point:last-child{margin-right:0}
.testimonial{width:100%;padding:50px;margin-bottom:100px;margin-right:25px;background-color:#74b9a5;color:#fcf8f6;position:relative;border-radius:10px;box-sizing:border-box;box-shadow:3px 3px 0 0 #68ab97;opacity:0}@media screen and (max-width: 500px){.testimonial{padding:30px}}.testimonial h3{font-size:32px;white-space:pre-wrap;line-height:1.5;margin-bottom:20px}@media screen and (max-width: 500px){.testimonial h3{font-size:24px}}.testimonial h3 span{font-size:200%;line-height:1}.testimonial h3 span:first-child{margin-right:10px}.testimonial h3 span:nth-child(2){margin-left:10px}.testimonial p{margin-bottom:10px;margin-left:20px;font-size:16px}@media screen and (max-width: 500px){.testimonial p{margin-left:10px}}.testimonial p.name{font-weight:bold !important}
